@import url(https://fonts.googleapis.com/css?family=Barlow&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #ebd8d0;
  font-family: "Barlow";
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding-top: 70px;
}

body.inGame {
  overflow: hidden;
}

.Menubar {
  background: white;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin-bottom: 20px;
}

.Menubar h1 {
  font-size: 22px;
  font-weight: bold;
}

.Menubar h1 em {
  font-weight: 300;
  font-style: normal;
  color: #f05e6c;
  padding-left: 1px;
}

.Menubar a {
  text-decoration: none;
  color: inherit;
}

.Menubar .nav {
  outline: none;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #e5e5e5;
  border-radius: 999em;
  font-weight: bold;
  padding: 4px 20px 5px;
  font-family: inherit;
  font-size: inherit;
}

.Menubar .nav:hover {
  background-color: #e5e5e5;
}

button.skip {
  background: white;
  border-radius: 999em;
  font-size: 14px;
  font-weight: bold;
  padding: 10px 15px;
  border: 0;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  outline: none;
  -webkit-transition: 0.1s -webkit-transform ease-out;
  transition: 0.1s -webkit-transform ease-out;
  transition: 0.1s transform ease-out;
  transition: 0.1s transform ease-out, 0.1s -webkit-transform ease-out;
  position: absolute;
  bottom: 20px;
  left: 50%;
  -webkit-transform: translateX(-50%) scale(1);
          transform: translateX(-50%) scale(1);
}

button.skip:hover {
  cursor: pointer;
  background-color: #f5f5f5;
}

button.skip:active {
  -webkit-transform: translateX(-50%) scale(0.95);
          transform: translateX(-50%) scale(0.95);
}

.Card {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 10px;
  position: absolute;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  text-align: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  max-width: 600px;
  width: 90vw;
  left: 50%;
  top: 50%;
}

.Card h2 {
  margin: 0;
  padding-top: 25px;
}

.Card p {
  font-size: 1.3em;
  padding: 15px 20px;
  padding-top: 0;
}

.Card button {
  border: 2px solid #e5e5e5;
  background: transparent;
  border-radius: 999em;
  padding: 10px 20px;
  outline: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 1em;
  margin: 0 5px 30px;
  align-self: center;
  -webkit-transition: 150ms all ease-out;
  transition: 150ms all ease-out;
}

.Card button.primary {
  border-color: #6ace97;
  background-color: #6ace97;
  color: white;
}

.Card button.primary:hover,
.Card button.primary:active {
  border-color: #5db886;
  background-color: #5db886;
}

.Card button:hover,
.Card button:active {
  background-color: #e5e5e5;
}

.Card button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.GameCard {
  cursor: -webkit-grab;
  cursor: grab;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

.GameCard.swiping {
  cursor: -webkit-grabbing;
  cursor: grabbing;
  box-shadow: 3px 3px 13px rgba(0, 0, 0, 0.2);
}

.GameCard .question {
  flex-grow: 1;
}

.GameCard .question p {
  padding-bottom: 0;
}

.Card.GameCard button {
  font-size: 30px;
  width: 70px;
  height: 70px;
  padding: 0;
  margin: 30px 15px;
}

.Card.GameCard button.positive-active {
  background-color: #6ace97;
  border-color: #6ace97;
}

.Card.GameCard button.negative-active {
  background-color: #f05e6c;
  border-color: #f05e6c;
}

.GameCard .info {
  border-bottom: 1px solid #e5e5e5;
  padding: 10px;
  font-weight: bold;
  font-size: 0.9em;
}

.GameCard .tag {
  background: #e5e5e5;
  font-weight: bold;
  font-size: 600;
  border-radius: 999em;
  font-size: 0.9em;
  padding: 2px 10px;
  margin-right: 3px;
  margin-left: 3px;
}

.Results {
}

.Results ul {
  padding: 0;
}

.Results ol {
  padding: 0;
  margin: 0;
  margin-top: 25px;
}

.Results li {
  text-align: left;
  list-style: none;
  padding: 0;
}

.Results li h3 {
  margin-bottom: 10px;
  margin-top: 5px;
}

.Results ol > li {
  border-top: 1px solid #e5e5e5;
  padding: 15px;
}

.Results li.different {
  opacity: 0.5;
}

.Results .partyName {
  flex-grow: 1;
}

.ResultRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 1.3em;
  height: 50px;
}

.ResultRow img {
  margin-right: 10px;
  width: 50px;
}

.Results .answer {
  display: flex;
  flex-direction: row;
  margin-top: 8px;
}

.Results .answer .explanation::before {
  content: ": ";
}

.Results .answer .emoji {
  flex-shrink: 0;
  position: relative;
  top: -0.2em;
  font-size: 1.3em;
  width: 1.3em;
}

.ContentCard {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 70px;
  position: static;
  -webkit-transition-duration: 500ms;
          transition-duration: 500ms;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  text-align: left;
}

.ContentCard h2 {
  text-align: center;
}

